        'cmsPages',
        'loggedIn'
                
        
        
    ]),
    numbers() {
      // Generate an array of numbers from 1 to the specified limit
      return Array.from({ length: this.rating_limit }, (_, index) => index + 1);
    },

  },
  data() {
    return {
        isLoading: true,
        slider_img: '',
        slider_content: '',
        slider_search_panel_margin_top: '',
        slider_search_panel_margin_bottom: '',
        slider_text_color: '',
        searchForm: {
            keyword: null,
            category_id: null,
            location_id: null,
        },
        search_category:[],
        search_states:[],
        live_jobs_stats:'',
        companies_stats:'',
        candidates_stats:'',
        new_jobs_stats:'',
        trending_categories:[],
        jobs_latest: [],
        jobs_featured: [],
        featured_jobs:[],
        top_companies: [],
        home_testimonials: [],

        jobs_list: [],
        rating_limit: 5,
    }
  },
  methods: {

    search(){
        this.$router.push({
            name: 'job-search',
            query: this.searchForm
        });
    },
    changeCategory(event:any){
        this.searchForm.category_id = event.value
    },
    changeLocation(event:any){
        this.searchForm.location_id = event.value
    },
    getJobDetail(job_key:any, job_slug:any) {    
      return {
        path: '/job-details/'+job_key+'/'+job_slug
        // path: '/job-details/${job_key}/${job_slug}'
      };
    },
    getCategoryJobs(cat_slug:any) {
      return {
        path: '/category-jobs/'+cat_slug
      };
    },
    getCompanyDetail(company_id:any) {
      return {
        path: '/company-details/'+company_id
      };
    },
    quoteRemove(text:any) {
      return text.replace('"', '');
    },

    parsedExpirationDate(expirationDate:any) {
        // Split the expiration date string into month, day, and year
        const [month, day, year] = expirationDate.split(' ');
        // Type annotation for monthMap
        const monthMap: { [key: string]: number } = {
            'Jan': 0, 'Feb': 1, 'Mar': 2, 'Apr': 3,
            'May': 4, 'Jun': 5, 'Jul': 6, 'Aug': 7,
            'Sep': 8, 'Oct': 9, 'Nov': 10, 'Dec': 11
        };

        // Create and return a Date object using the parsed month, day, and year
        return new Date(year, monthMap[month], parseInt(day, 10));
    },
        
    isJobExpired(expirationDate: any) {
        const today = new Date();
        const expiration = this.parsedExpirationDate(expirationDate);
        return expiration < today; // Returns true if the job is expired
    },

  },
  mounted(){

    this.$store.dispatch('getGlobalVariables', '');
    this.$store.dispatch('getCategories', '');
    this.$store.dispatch('getStates', ''); 
    this.$store.dispatch('getHomeStats', '');
    this.$store.dispatch('getTrendingJobCategories', '');
    this.$store.dispatch('getLatestJobs', '');
    this.$store.dispatch('getFeaturedJobs', '');
    this.$store.dispatch('getTopCompanies', '');
    this.$store.dispatch('getTestimonials', '');
    var credentials = {
        'page_slug': 'home',
    }
    this.$store.dispatch('getCMSPages', credentials);


    // this.$store.dispatch('getCategories', '');
    // this.$store.dispatch('getCompanies', '');
    // this.$store.dispatch('getLocations', '');  
    // this.$store.dispatch('getJobs', '');

    // this.$store.dispatch('getTestimonials', '');
    
    let Script = document.createElement("script");
    Script.setAttribute("src", "/assets/js/main.js");
    document.head.appendChild(Script);

  },
